.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html{
  background-color: #f3f3f3;
}

.eliForm{
  box-sizing: border-box;
  max-width: 90%;
  margin: 28px auto;
  padding: 55px;

  background-color:  #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);

  font: bold 14px sans-serif;
  text-align: center;
}

.eliForm .form-row{
  text-align: left;
  max-width: 90%;
  margin: 25px auto 0;
}

.eliForm .form-title-row{
  margin: 0 auto 40px;
}

.eliForm h1{
  display: inline-block;
  box-sizing: border-box;
  color: #4C565E;
  font-size: 24px;
  padding: 0 0 12px 0;
  margin: 0;
  border-bottom: 2px solid #6CAEE0;
}

.eliForm h2{
  display: inline-block;
  box-sizing: border-box;
  color: #4C565E;
  font-size: 14px;
  padding: 10px 5px 12px 5px;
  margin: 0;
  text-align: right;
}

.eliForm .form-row > label span{
  display: block;
  box-sizing: border-box;
  color:  #5f5f5f;
  width: 100%;
  padding: 0 0 12px;
}

.eliForm input{
  color:  #5f5f5f;
  box-sizing: border-box;
  width: 100%;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.08);
  padding: 12px 18px;
  border: 1px solid #dbdbdb;
}

.eliForm input[type=radio],
.eliForm input[type=checkbox]{
  box-shadow: none;
  width: auto;
}

.eliForm select{
  background-color: #ffffff;
  color:  #5f5f5f;
  box-sizing: border-box;
  width: 100%;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.08);
  padding: 12px 18px;
  border: 1px solid #dbdbdb;
}

.eliForm .form-radio-buttons > div{
  margin-bottom: 10px;
}

.eliForm .form-radio-buttons label span{
  margin-left: 8px;
  color:  #5f5f5f;
  font-weight: normal;
}

.eliForm .form-radio-buttons input{
  width: auto;
}

.eliForm button{
  border-radius: 2px;
  background-color:  #6caee0;
  color: #ffffff;
  font-weight: bold;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.08);
  padding: 14px 22px;
  border: 0;
  margin-top: 15px;
}

/*	Making the form responsive. Remove this media query
  if you don't need the form to work on mobile devices. */

@media (max-width: 600px) {

  .eliForm{
      padding: 30px;
  }

  .eliForm input{
      width: 240px;
  }

}
